:root {
  --primary-color: #D96AA7;
  --secondary-color: #732c2c;
  --complimentary-color: #88BFB5;
  --contrast-color: #F2E527;
  --light-color: #D2A9D9;
  }
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  background: #191919;;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.video-container {
  position: relative;
  display: flex;
  width: 100vw;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 50px); /* Reduce height to account for the tab menu */
  overflow: hidden;
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  opacity: 1;
  transform: scale(1);
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

img {
  margin: 20;
  padding: 10;
  width: 100%;
  height: 100%;
  object-fit: scale-down;
}

.tab-menu {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  background: var(--secondary-color);
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 20px;
  text-decoration: none;
  padding: 10px;
  text-align: center;
}

.tab {
  flex: 1;
  text-align: center;
  padding: 10px 0;
  cursor: none;
  border-radius: 15px;
  transition: all ease-in .5s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tab.active {
  font-size: 30px;
  color: #fff;
  text-decoration: none;
  padding: 15px;
  text-align: center;
  background-color: teal;
}

