} */

/* Custom transition for Tab 0 */
.tab-0 {
  background-color: #f8b400; /* Example color */
}

.tab-0.content-container {
  transform: translateX(0);
  opacity: 1;
}

/* Custom transition for Tab 1 */
.tab-1 {
  background-color: #0f9b8e;
}

.tab-1.content-container {
  transform: translateX(-100%);
  opacity: 0.8;
}

/* Custom transition for Tab 2 */
.tab-2 {
  background-color: #cf000f;
}

.tab-2.content-container {
  transform: translateX(100%);
  opacity: 0.6;
}

/* Add this to your App.css */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.video-container.fade-in {
  animation: fadeIn 0.5s ease-in-out;
}